<template lang="pug">
.of-second-background
    oc-section( sectionId='ma', title='Mujeres Adas sin "H"', :tabsContent='tabsContent',nextRef='#eventos', inverted=true )
</template>

<script>
import OcSection from '@/components/shared/Section.vue'
import SECTION from  '@/constants/section-types'

export default {
    components: { OcSection },
    data() {
        return {
            SECTION,
            tabsContent: [
                { 
                    id: 'whatis', 
                    label: 'Qu es?', 
                    active: true, 
                    content: { 
                        type: SECTION.DEFINITION,
                        img: `${HOMEURL}img/ma/logo.png`,
                        pUrl: 'https://www.youtube.com/embed/B1STXMJCZi0',
                        html: 'Es un curso, Taller y Libro creado y escrito por la Dra. Mirna Juraidini, para las mujeres en diferentes situaciones de status, belleza, economa y vida como casADAS, separADAS, divorciADAS, quedADAS, enviudADAS, y que se sientan pateADAS, por la vida, basadas en el desarrollo del cerebro de la mujer, sus acciones reacciones, pensamientos, inquietudes, dificultades etc.<br><br>Si quieres entenderte, aceptarte y REALIZARTE como Mujer ven o lee MUJERES ADAS SIN H'
                    }
                },
                { 
                    id: 'book', 
                    label: 'Libro', 
                    active: false,
                    content: {
                        type: SECTION.BOOK,
                        img: `${HOMEURL}img/ma/book.png`,
                        html: 'Enva tu foto a  para que lo recibas autografado por la autora. <a href="mailto:mirna@ortografiacerebral.com?Subject=Foto para mi libro autografado" target="_top">mirna@ortografiacerebral.com</a>',
                        payPalValue: 'LMUNBR3B5W424'
                    } 
                },
                { 
                    id: 'photos', 
                    label: 'Fotos', 
                    active: false,
                    content: {
                        type: SECTION.POTHOS,
                        timeInterval: 20000,
                        slides: [
                            `${HOMEURL}img/of/slides/1.jpg`,
                            `${HOMEURL}img/of/slides/2.jpg`,
                            `${HOMEURL}img/of/slides/3.jpg`,
                            `${HOMEURL}img/of/slides/4.jpg`,
                            `${HOMEURL}img/of/slides/5.jpg`
                        ]
                    } 
                }
            ]
        }
    }
}
</script>


