<template lang="pug">
oc-section( sectionId='oc', title='Ortografa Cerebral', :tabsContent='tabsContent',nextRef='#of')
</template>

<script>
import OcSection from '@/components/shared/Section.vue'
import SECTION from  '@/constants/section-types'

export default {
    components: { OcSection },
    data() {
        return {
            SECTION,
            tabsContent: [
                { 
                    id: 'whatis', 
                    label: 'Qu es?', 
                    active: true, 
                    content: { 
                        type: SECTION.DEFINITION,
                        img: `${HOMEURL}img/oc/logo.png`,
                        pUrl: 'https://www.youtube.com/embed/355mFGdGJls',
                        html: 'Es una tcnica creada por la Dra. Mirna Juraidini desde 1998, basada en Neurofisiologa, Qumica Cerebral, Ingeniera del Pensamiento, P.N.L. y algunas terapias alternativas. Te apoyar a descubrir la informacin especfica que est almacenada en el cerebro reptil (tu inconsciente) grabada en un instante de tu vida que fue tapada por tu cerebro, y que te ha llevado a ser y tener lo que has vivido hasta este momento.<br/><br/>Al descubrir y reconocer se solucionan problemas o situaciones que enfrentas. Tus pensamientos creados y aprendidos desde tu infancia son el resultado de lo que hoy eres y sers. Cada persona es el resultado de sus experiencias; pues cada una de nuestras ideas y pensamiento est creando nuestro futuro. Si los resultados que has obtenido hasta este momento de tu vida te agradan y te han dado buenos resultados, te felicito, puedes seguir como estas, pero si estas en la bsqueda, de nuevos resultados y quieres llegar a tener xito, felicidad, armona VEN Y DESCUBRE TU REALIDAD con ORTOGRAFA CEREBRAL!'
                    }
                },
                { 
                    id: 'book', 
                    label: 'Libro', 
                    active: false,
                    content: {
                        type: SECTION.BOOK,
                        img: `${HOMEURL}img/oc/book.png`,
                        html: 'Enva tu foto a  para que lo recibas autografado por la autora. <a href="mailto:mirna@ortografiacerebral.com?Subject=Foto para mi libro autografado" target="_top">mirna@ortografiacerebral.com</a>',
                        payPalValue: 'CRA6TTAM7B5EC'
                    } 
                },
                { 
                    id: 'photos', 
                    label: 'Fotos', 
                    active: false,
                    content: {
                        type: SECTION.POTHOS,
                        timeInterval: 20000,
                        slides: [
                            `${HOMEURL}img/oc/slides/1.jpg`
                        ]
                    } 
                }
            ]
        }
    }
}
</script>


